// src/components/PrimeIdentifier.js
import React, { useState, useEffect, useRef, useCallback } from "react";
import { leagueColorMapping } from "../utils/constants";
import ContextInfo from "./ContextInfo";
import _ from "lodash";
import toast, { Toaster } from "react-hot-toast";
import "../styles/PrimeIdentifier.css";

// --- Constants ---
const DEFAULT_THRESHOLD_PCT = 70;
const DEFAULT_GAMES_PCT_THRESHOLD = 100;
const MAX_CUSTOM_DEFINITIONS = 5; // Note: Max check might be better enforced in Dashboard
const ORIGINAL_DEFINITION_ID = "original_data_definition";
const API_ENDPOINT =
  "https://peakperformr-api-final.yellowcliff-d82a2703.westus2.azurecontainerapps.io/recalculate";
const ESTIMATED_API_TIME = 90; // seconds

// --- Utility Functions ---
const getParamsKey = (params) =>
  `t${params.thresholdPct}_g${params.gamesPctThreshold}`;

const calculateLeaguePrimeStats = (primes) => {
  if (!Array.isArray(primes) || primes.length === 0) return [];
  const leagueGroups = _.groupBy(primes, "league");
  const stats = Object.entries(leagueGroups)
    .map(([league, playersInLeague]) => {
      if (!playersInLeague || playersInLeague.length === 0) return null;

      const validDurations = playersInLeague
        .map((p) => p?.prime_duration)
        .filter((d) => typeof d === "number" && isFinite(d));
      const avgDuration =
        validDurations.length > 0 ? _.mean(validDurations) : 0;

      const validStartAges = playersInLeague
        .map((p) => p?.start_age)
        .filter((a) => typeof a === "number" && isFinite(a));
      const avgStartAge =
        validStartAges.length > 0 ? _.mean(validStartAges) : 0;

      const validEndAges = playersInLeague
        .map((p) => p?.end_age)
        .filter((a) => typeof a === "number" && isFinite(a));
      const avgEndAge = validEndAges.length > 0 ? _.mean(validEndAges) : 0;

      return {
        league: league,
        avgDuration: avgDuration,
        avgStartAge: avgStartAge,
        avgEndAge: avgEndAge,
        playerCount: playersInLeague.length,
        color: leagueColorMapping[league] || "#cccccc",
      };
    })
    .filter(Boolean); // Remove null entries if any league had no valid players/data
  return _.orderBy(stats, ["playerCount", "avgDuration"], ["desc", "desc"]);
};

// --- Main Component ---
const PrimeIdentifier = ({
  originalData, // Base data for calculations if needed (API might not need it)
  availableDefinitions: globalAvailableDefinitions, // Definitions from Dashboard
  activeDefinitionKey: globalActiveDefinitionKey, // Active key from Dashboard
  onPrimeChange, // Callback to Dashboard when new prime data is calculated
  onDefinitionSelect, // Callback to Dashboard to request changing the global active definition
  getColorForDefinition, // Utility from Dashboard
}) => {
  // Local state for sliders ONLY
  const [thresholdPct, setThresholdPct] = useState(DEFAULT_THRESHOLD_PCT);
  const [gamesPctThreshold, setGamesPctThreshold] = useState(
    DEFAULT_GAMES_PCT_THRESHOLD
  );

  // State for API call management
  const [activeApiRequests, setActiveApiRequests] = useState({}); // Tracks loading state by paramsKey
  const [apiErrors, setApiErrors] = useState({}); // Tracks errors by paramsKey
  const [countdownTimer, setCountdownTimer] = useState(0);
  const countdownIntervalRef = useRef(null);

  // State for the data currently being displayed (derived from global state)
  const [displayData, setDisplayData] = useState({
    rawPrimes: [],
    splinePrimes: [],
    pqi: [],
    cqi: [],
    fullData: [],
    leagueStats: [],
    label: "N/A", // Label of the definition being viewed
  });

  // --- Effects ---

  // 1. Synchronize local view with global state (active definition key and list)
  useEffect(() => {
    console.log(
      `PrimeIdentifier Effect: Syncing view to global key "${globalActiveDefinitionKey}"`
    );
    const currentGlobalDef = globalAvailableDefinitions?.find(
      (def) => def.id === globalActiveDefinitionKey
    );

    if (currentGlobalDef) {
      console.log(
        `PrimeIdentifier Effect: Found definition: ${currentGlobalDef.label}`
      );
      // Determine the data source for display
      let sourceData;
      if (currentGlobalDef.id === ORIGINAL_DEFINITION_ID) {
        // Use the original data prop directly for the original definition
        sourceData = originalData;
      } else {
        // Use the cached primeData if available, otherwise fallback (should ideally not happen if Dashboard logic is correct)
        sourceData = currentGlobalDef.primeData || originalData;
        if (!currentGlobalDef.primeData) {
          console.warn(
            `PrimeIdentifier: Definition ${currentGlobalDef.id} has no cached primeData. Displaying original data as fallback.`
          );
        }
      }

      // Ensure sourceData and its properties are valid before accessing
      if (
        sourceData &&
        sourceData.primesRawData &&
        sourceData.primesSplineData &&
        sourceData.pqiData &&
        sourceData.cqiData &&
        sourceData.fullData
      ) {
        setDisplayData({
          rawPrimes: sourceData.primesRawData || [],
          splinePrimes: sourceData.primesSplineData || [],
          pqi: sourceData.pqiData || [],
          cqi: sourceData.cqiData || [],
          fullData: sourceData.fullData || [],
          leagueStats: calculateLeaguePrimeStats(
            sourceData.primesSplineData || []
          ),
          label: currentGlobalDef.label || "Unnamed Definition",
        });

        // Update sliders to match the parameters of the viewed definition
        if (
          currentGlobalDef.parameters &&
          currentGlobalDef.id !== ORIGINAL_DEFINITION_ID
        ) {
          setThresholdPct(
            currentGlobalDef.parameters.thresholdPct ?? DEFAULT_THRESHOLD_PCT
          );
          setGamesPctThreshold(
            currentGlobalDef.parameters.gamesPctThreshold ??
              DEFAULT_GAMES_PCT_THRESHOLD
          );
        } else if (currentGlobalDef.id === ORIGINAL_DEFINITION_ID) {
          // Optionally reset sliders to default when viewing original, or keep them as they were
          // Resetting might be clearer:
          setThresholdPct(DEFAULT_THRESHOLD_PCT);
          setGamesPctThreshold(DEFAULT_GAMES_PCT_THRESHOLD);
        }
        console.log(
          `PrimeIdentifier Effect: Display updated for ${currentGlobalDef.label}`
        );
      } else {
        console.error(
          `PrimeIdentifier Effect: Source data for definition ${currentGlobalDef.id} is incomplete or invalid. Clearing display.`
        );
        // Clear display if data is bad
        setDisplayData({
          rawPrimes: [],
          splinePrimes: [],
          pqi: [],
          cqi: [],
          fullData: [],
          leagueStats: [],
          label: "Error: Invalid Data",
        });
      }
    } else {
      console.warn(
        `PrimeIdentifier Effect: Active definition key "${globalActiveDefinitionKey}" not found in global list. Displaying N/A.`
      );
      // Clear display or show error state if active definition not found
      setDisplayData({
        rawPrimes: [],
        splinePrimes: [],
        pqi: [],
        cqi: [],
        fullData: [],
        leagueStats: [],
        label: "N/A",
      });
    }
  }, [globalActiveDefinitionKey, globalAvailableDefinitions, originalData]); // Re-run when global state changes

  // 2. Countdown timer cleanup
  useEffect(() => {
    return () => {
      if (countdownIntervalRef.current) {
        clearInterval(countdownIntervalRef.current);
      }
    };
  }, []);

  // --- Event Handlers ---

  // Update local slider state only
  const handleParameterChange = (setter) => (event) => {
    setter(parseInt(event.target.value, 10));
  };

  const startCountdown = (seconds) => {
    if (countdownIntervalRef.current)
      clearInterval(countdownIntervalRef.current);
    setCountdownTimer(seconds);
    countdownIntervalRef.current = setInterval(() => {
      setCountdownTimer((prevTime) => {
        if (prevTime <= 1) {
          clearInterval(countdownIntervalRef.current);
          return 0;
        }
        return prevTime - 1;
      });
    }, 1000);
  };

  // Trigger API Call and notify Dashboard on completion
  const handleCalculateRequest = useCallback(async () => {
    const currentParams = { thresholdPct, gamesPctThreshold };
    const paramsKey = getParamsKey(currentParams);

    if (activeApiRequests[paramsKey]) return; // Already requesting

    // Check if this definition already exists globally
    const existingGlobalDef = globalAvailableDefinitions?.find(
      (def) => def.id === paramsKey
    );
    if (existingGlobalDef) {
      toast.success(
        `Definition already exists: ${existingGlobalDef.label}. Switching view.`
      );
      if (onDefinitionSelect) {
        onDefinitionSelect(paramsKey); // Ask Dashboard to switch view
      }
      return;
    }

    // Check max definitions (Dashboard should ideally enforce this, but double-check)
    const customDefCount =
      globalAvailableDefinitions?.filter(
        (def) => def.id !== ORIGINAL_DEFINITION_ID
      ).length || 0;
    if (customDefCount >= MAX_CUSTOM_DEFINITIONS) {
      toast.error(
        `Maximum of ${MAX_CUSTOM_DEFINITIONS} custom definitions reached.`
      );
      return;
    }

    setActiveApiRequests((prev) => ({ ...prev, [paramsKey]: "loading" }));
    setApiErrors((prev) => {
      const n = { ...prev };
      delete n[paramsKey];
      return n;
    });
    const toastId = toast.loading(
      `Calculating: T=${thresholdPct}% / G=${gamesPctThreshold}%...`
    );
    startCountdown(ESTIMATED_API_TIME);

    try {
      const response = await fetch(API_ENDPOINT, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(currentParams),
      });

      if (!response.ok) {
        const errorText = await response.text();
        throw new Error(`API failed (${response.status}). ${errorText}`);
      }
      const results = await response.json();

      if (results?.success) {
        const newLabel = `Custom: T=${results.parameters.thresholdPct}% / G=${results.parameters.gamesPctThreshold}%`;
        const newDefinitionData = {
          definitionKey: paramsKey, // Pass the key
          definitionLabel: newLabel,
          definitionParams: results.parameters,
          raw: results.rawPrimes || [],
          spline: results.splinePrimes || [],
          pqi: results.pqi || [],
          cqi: results.cqi || [],
          fullData: results.fullData || [], // Include recalculated fullData if API provides it
          isOriginal: false,
          // Let Dashboard assign color based on the updated global list
        };

        toast.success(`Calculation complete for ${newLabel}`, { id: toastId });

        // Notify Dashboard of the new data and definition details
        if (onPrimeChange) {
          onPrimeChange(newDefinitionData);
          console.log("Notified Dashboard of new definition:", paramsKey);
        }
        // Dashboard will handle adding it to the list and setting it as active

        clearInterval(countdownIntervalRef.current);
        setCountdownTimer(0);
      } else {
        throw new Error(results?.message || "API returned unsuccessful data.");
      }
    } catch (error) {
      console.error(`API Call failed for ${paramsKey}:`, error);
      setApiErrors((prev) => ({ ...prev, [paramsKey]: error.message }));
      toast.error(`Calculation failed: ${error.message}`, { id: toastId });
      clearInterval(countdownIntervalRef.current);
      setCountdownTimer(0);
    } finally {
      setActiveApiRequests((prev) => {
        const n = { ...prev };
        delete n[paramsKey];
        return n;
      });
    }
  }, [
    thresholdPct,
    gamesPctThreshold,
    activeApiRequests,
    globalAvailableDefinitions,
    onPrimeChange,
    onDefinitionSelect, // Depend on global state and callbacks
  ]);

  // Handle selecting a definition from the dropdown - Request global change
  const handleSelectDefinition = (event) => {
    const selectedKey = event.target.value;
    if (selectedKey !== globalActiveDefinitionKey && onDefinitionSelect) {
      console.log(
        `PrimeIdentifier: Requesting global definition change to: ${selectedKey}`
      );
      onDefinitionSelect(selectedKey); // Ask Dashboard to change the active definition
      // Clear local API error for the newly selected key if any
      setApiErrors((prev) => {
        const n = { ...prev };
        delete n[selectedKey];
        return n;
      });
    }
  };

  // REMOVED: handleApplySelected - Redundant with global DefinitionSelector and dropdown calling onDefinitionSelect
  // REMOVED: handleDeleteDefinition - Definition management should be centralized in Dashboard

  // --- Derived State for Rendering ---
  const isLoadingAny = Object.values(activeApiRequests).includes("loading");
  const customDefCount =
    globalAvailableDefinitions?.filter(
      (def) => def.id !== ORIGINAL_DEFINITION_ID
    ).length || 0;
  const canCalculate = !isLoadingAny && customDefCount < MAX_CUSTOM_DEFINITIONS;
  const currentParamsKey = getParamsKey({ thresholdPct, gamesPctThreshold });
  const isCurrentParamLoading =
    activeApiRequests[currentParamsKey] === "loading";
  // Check if the params match an *existing* global definition
  const isCurrentParamAvailable = !!globalAvailableDefinitions?.some(
    (def) => def.id === currentParamsKey
  );

  const calculateButtonTooltip = isCurrentParamLoading
    ? `Calculating... (${countdownTimer}s remaining)`
    : isCurrentParamAvailable
    ? `Definition with these parameters already exists.`
    : !canCalculate
    ? `Maximum of ${MAX_CUSTOM_DEFINITIONS} custom definitions reached.`
    : `Calculate new data with T=${thresholdPct}% / G=${gamesPctThreshold}%. (Est. ${ESTIMATED_API_TIME}s)`;

  // Options for the dropdown, using global definitions
  const definitionOptions =
    globalAvailableDefinitions
      ?.sort(
        (a, b) =>
          a.id === ORIGINAL_DEFINITION_ID
            ? -1
            : b.id === ORIGINAL_DEFINITION_ID
            ? 1
            : (a.timestamp || 0) - (b.timestamp || 0) // Sort by timestamp if available
      )
      .map((def) => ({
        value: def.id,
        label: `${def.label}${
          activeApiRequests[def.id] ? " (Calculating...)" : ""
        }${apiErrors[def.id] ? " (Error!)" : ""}`,
        error: apiErrors[def.id] || null, // Keep track of API errors locally
      })) || []; // Default to empty array if global definitions not ready

  // Stats based on currently displayed data (from displayData state)
  const avgSplineDuration = (
    displayData.splinePrimes.reduce(
      (sum, p) => sum + (p.prime_duration || 0),
      0
    ) / (displayData.splinePrimes.length || 1)
  ).toFixed(1);
  const splineCount = displayData.splinePrimes.length;

  // --- Render ---
  return (
    <div className="prime-identifier component-box">
      <Toaster position="top-right" containerStyle={{ zIndex: 9999 }} />
      <div className="section-header">
        <h2>
          Prime Definition Explorer
          <ContextInfo title="Prime Definition Explorer" description="..." />
        </h2>
        <span
          className={`status-indicator ${
            globalActiveDefinitionKey === ORIGINAL_DEFINITION_ID
              ? "original"
              : "modified"
          }`}
        >
          {/* Use label from displayData state, which is synced with global */}
          Viewing: {displayData.label}
        </span>
        {/* Display API error related to the currently viewed definition if any */}
        {apiErrors[globalActiveDefinitionKey] && (
          <div className="api-error-message inline-error">
            Error loading this definition:{" "}
            {apiErrors[globalActiveDefinitionKey]}
          </div>
        )}
      </div>

      {/* --- Settings Panel --- */}
      <div className="settings-panel">
        {/* Parameters (Sliders control local state) */}
        <div className="threshold-controls control-group">
          {/* Performance Threshold Slider */}
          <div className="slider-container">
            <label htmlFor="threshold-pct">
              Performance Threshold (%): {thresholdPct}%{" "}
              <ContextInfo title="Performance Threshold" description="..." />
            </label>
            <div className="slider-with-labels">
              <div className="slider-label left green">
                More Flexible Prime <span className="arrow"></span>
              </div>
              <input
                type="range"
                id="threshold-pct"
                min="50"
                max="100"
                step="5"
                value={thresholdPct}
                onChange={handleParameterChange(setThresholdPct)}
                disabled={isLoadingAny}
              />
              <div className="slider-label right red">
                <span className="arrow"></span> Stricter Prime
              </div>
            </div>
          </div>
          {/* Games Played Threshold Slider */}
          <div className="slider-container">
            <label htmlFor="games-pct-threshold">
              Low Games Skip (%ile): {gamesPctThreshold}%{" "}
              <ContextInfo title="Games Played Threshold" description="..." />
            </label>
            <div className="slider-with-labels">
              <div className="slider-label left red">
                <span className="arrow"></span>
              </div>

              <input
                type="range"
                id="games-pct-threshold"
                min="0"
                max="100"
                step="5"
                value={gamesPctThreshold}
                onChange={handleParameterChange(setGamesPctThreshold)}
                disabled={isLoadingAny}
              />
              <div className="slider-label right green">
                <span className="arrow"></span> More Flexible Prime
              </div>
            </div>
          </div>
        </div>

        {/* Definition Management */}
        <div className="definition-management">
          {/* Dropdown uses global state, onChange requests global change */}
          <div className="control-group definition-selector">
            <label htmlFor="definition-select">View Definition:</label>
            <select
              id="definition-select"
              value={globalActiveDefinitionKey} // Controlled by global state
              onChange={handleSelectDefinition} // Requests global change
              disabled={isLoadingAny}
            >
              {definitionOptions.map((opt) => (
                <option
                  key={opt.value}
                  value={opt.value}
                  title={opt.error || ""}
                >
                  {opt.label}
                </option>
              ))}
            </select>
            {apiErrors[globalActiveDefinitionKey] && ( // Show error if the *selected* definition had a load error
              <span className="error-text"> (Load Error!)</span>
            )}
          </div>
          {/* Action Buttons */}
          <div className="action-buttons">
            <button
              onClick={handleCalculateRequest}
              disabled={
                !canCalculate ||
                isCurrentParamLoading ||
                isCurrentParamAvailable
              }
              className={`calculate-button ${
                isCurrentParamLoading ? "loading" : ""
              }`}
              title={calculateButtonTooltip}
            >
              {isCurrentParamLoading
                ? `Calculating... (${countdownTimer}s)`
                : "Calculate & Add"}
            </button>
            {/* REMOVED Apply Selected Globally button - use main DefinitionSelector */}
          </div>
        </div>
        {/* REMOVED Saved Definitions List with Delete - Handled by global DefinitionSelector */}
      </div>

      {/* --- Global Stats (Based on Displayed Data) --- */}
      <div className="global-stats-summary component-box">
        {/* Use label from displayData state */}
        <h4>Overall Stats (Viewing: {displayData.label})</h4>
        <div className="stat-cards">
          <div className="stat-card">
            Players (Spline Primes): <span>{splineCount}</span>
          </div>
          <div className="stat-card">
            Avg. Prime Duration (Spline): <span>{avgSplineDuration} yrs</span>
          </div>
        </div>
      </div>

      {/* --- League Comparison (Based on Displayed Data) --- */}
      {displayData.leagueStats.length > 0 && (
        <div className="league-prime-comparison component-box">
          <h4>
            League Comparison{" "}
            <ContextInfo title="League Prime Comparison" description="..." />
          </h4>
          <table>
            <thead>
              <tr>
                <th>League</th>
                <th>Players</th>
                <th>Avg Start Age</th>
                <th>Avg End Age</th>
                <th>Avg Duration</th>
              </tr>
            </thead>
            <tbody>
              {displayData.leagueStats.map((lg) => (
                <tr key={lg.league}>
                  <td>
                    <span
                      className="color-swatch"
                      style={{ backgroundColor: lg.color }}
                    ></span>
                    {lg.league}
                  </td>
                  <td>{lg.playerCount}</td>
                  <td>{lg.avgStartAge?.toFixed(1)}</td>
                  <td>{lg.avgEndAge?.toFixed(1)}</td>
                  <td>{lg.avgDuration?.toFixed(1)}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}

      {/* --- Cross-Definition Comparison Table --- */}
      {/* This table now iterates over the global definitions passed via props */}
      {globalAvailableDefinitions &&
        globalAvailableDefinitions.length > 1 && ( // Show only if more than original exists
          <div className="definitions-comparison component-box">
            <h4>
              Cross-Definition Comparison{" "}
              <ContextInfo title="Definition Comparison" description="..." />
            </h4>
            <table className="comparison-table">
              <thead>
                <tr>
                  <th>Definition</th>
                  <th>Players</th>
                  <th>Avg Start Age</th>
                  <th>Avg End Age</th>
                  <th>Avg Duration</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody>
                {globalAvailableDefinitions.map((def) => {
                  // Use the data associated with the definition (original or cached primeData)
                  const dataSource =
                    def.id === ORIGINAL_DEFINITION_ID
                      ? originalData
                      : def.primeData;
                  const primesToAnalyze = dataSource?.primesSplineData || []; // Use spline primes for stats
                  const defStats = calculateLeaguePrimeStats(primesToAnalyze);

                  const avgStart =
                    defStats.length > 0
                      ? _.meanBy(defStats, "avgStartAge").toFixed(1)
                      : "N/A";
                  const avgEnd =
                    defStats.length > 0
                      ? _.meanBy(defStats, "avgEndAge").toFixed(1)
                      : "N/A";
                  const avgDur =
                    defStats.length > 0
                      ? _.meanBy(defStats, "avgDuration").toFixed(1)
                      : "N/A";
                  const totalPlayers = defStats.reduce(
                    (sum, stat) => sum + stat.playerCount,
                    0
                  );

                  return (
                    <tr
                      key={def.id}
                      className={
                        def.id === globalActiveDefinitionKey ? "active-row" : ""
                      }
                    >
                      <td>{def.label}</td>
                      <td>{totalPlayers || "N/A"}</td>
                      <td>{avgStart}</td>
                      <td>{avgEnd}</td>
                      <td>{avgDur}</td>
                      <td>
                        <button
                          onClick={() =>
                            onDefinitionSelect && onDefinitionSelect(def.id)
                          } // Request global view change
                          className="view-button"
                          disabled={
                            def.id === globalActiveDefinitionKey ||
                            !!activeApiRequests[def.id]
                          } // Disable if active or loading
                        >
                          View
                        </button>
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </div>
        )}
    </div>
  );
};

export default PrimeIdentifier;
